.venue-section {
    position: relative;
    height: 100vh;
    background-image: url('../static/background2.jpg');
    background-size: cover;
    background-position: center;
  }
  
  .venue-section .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
  }
  
  .venue-title {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
  
  .venue-details {
    font-size: 1.5rem;
  }