.landing-section {
    position: relative;
    height: 100vh;
    background-image: url('../static/background-2.jpg');
    background-size: cover;
    background-position: center;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }

  .left-section {
    width: 40%;
    float: left;
  }

  .right-section {
    float: right; 
    width: 60%;
    /* background-image: url('../static/background-2.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 5%; */
    height: 100%;
  }
  
  .welcome-text {
    font-size: 4rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
    font-family: weddingFontRegular;
  }

  .phrase2-text {
    font-size: 3rem;
